.entry {
background-color: mistyrose;
padding: 5px;
margin: 10px;
display: flex;
width: 100%;
border-radius: 10px;
}

.todo-list{
    background-color: rgb(217, 193, 240);
padding: 10px;
margin: 10px;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
width: 50%;
border-radius: 20px;
position: absolute;
top: 100px;
left: 50%;
transform: translateX(-50%);
}

.todo-list h2{
    color: purple;
    text-align: center;
    background-color: transparent;
}

.todo-list input{
    width: 100%;
    font-size: 18px;
    padding: 10px;
    border-radius: 10px;
    border: none;
}
.todo-list input:focus{
    border: none;
    outline: none;
}

.todo-list button{
    width: 100px;
    border: none;
    border-radius: 10px;
    background-color: rgb(82, 211, 82);
    color: white;
    font-weight: 900;
    margin-left: 10px;
}

.entry p {
    width: 100%;
    font-size: 18px;
    padding: 10px;
    border-radius: 10px;

}
.entry button{
    width: 100px;
    border: none;
    border-radius: 10px;
    background-color: tomato;
    color: white;
    font-weight: 900;
    margin-left: 10px;

}
.entry input{
    width: 100%;
    font-size: 18px;
    padding: 10px;
    border-radius: 10px;
    border: none;
}
.entry input:focus{
    border: none;
    outline: none;
}
.entry .edit {
    background-color: dodgerblue;
}
.entry .edit input{
    background-color: white;
}
.entry .update {
    background-color: white;
    color: teal;
}
